.program-example {
  background-color: $light-grey;

  @media (min-width: $tablet-width) {
    background-image: linear-gradient($white 0%, $white 58%, $light-grey 58%, $light-grey 100%);
    border-top: $border-menu;
  }

  @media (min-width: $desktop-width) {
    background-image: linear-gradient($white 0%, $white 31%, $light-grey 31%, $light-grey 100%);
    border-top: none;
  }
}

.program-example__title {
  padding-top: 27px;
  padding-bottom: 35px;
  font-size: 36px;
  line-height: 40px;

  @media (min-width: $tablet-width) {
    padding-top: 47px;
    padding-bottom: 61px;
    font-size: 60px;
    line-height: 64px;
    background-color: $white;
  }

  @media (min-width: $desktop-width) {
    padding-top: 67px;
    padding-bottom: 73px;
  }
}

.program-example__wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
    padding: 0 calc(50% - 610px);
    background-color: $light-grey;
  }
}

.program-example__description {
  @media (min-width: $tablet-width) {
    background-color: $white;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    background-color: $light-grey;
  }
}

.program-example__text {
  margin-bottom: 27px;
  font-size: 14px;
  line-height: 18px;
  color: $special_dark-grey;

  @media (max-width: $mobile-width-only) {
    .carryover {
      display: none;
    }
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 52px;
    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 68px;
    padding-top: 72px;
    width: 436px;

    .carryover {
      display: none;
    }
  }
}

.program-example__stats-wrapper {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
    align-items: flex-start;

    .carryover {
      display: none;
    }
  }
}

.program-example__stats {
  display: flex;
  justify-content: space-between;
}

.program-example__stats-index {
  position: relative;
  min-width: 124px;
  min-height: 56px;
  border: $border-input-hover;
  border-radius: 3px;

  @media (min-width: $tablet-width) {
    margin-right: 42px;
    margin-bottom: 5px;
    min-width: 162px;
    min-height: 72px;
  }
}

.program-example__index-number {
  padding-top: 11px;
  margin-bottom: 4px;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 28px;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding-top: 13px;
    margin-bottom: 12px;
    font-size: 30px;
    line-height: 37px;
  }
}

.program-example__index-description {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-left: 0;
  min-width: 84px;
  font-size: 12px;
  line-height: 14px;
  color: $special_dark-grey;
  background-color: $light-grey;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding: 0 7px;
    width: max-content;
    max-width: 142px;
    font-size: 14px;
    line-height: 16px;
    background-color: $white;

    .carryover {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    background-color: $light-grey;
  }
}

.program-example__cost-text {
  margin-top: 33px;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;

  @media (max-width: $mobile-width-only) {
    .carryover {
      display: none;
    }
  }

  @media (min-width: $tablet-width) {
    margin-top: 0;
    font-size: 20px;
    line-height: 30px;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    margin-top: 51px;
  }
}

.program-example__cost-text-price {
  @media (min-width: $desktop-width) {
    padding-left: 60px;
  }
}

.program-example__slider {
  @media (min-width: $tablet-width) {
    background-image: linear-gradient($white 0%, $white 26%, $light-grey 26%, $light-grey 100%);
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin-left: 98px;
    margin-top: -191px;
    background-image: none;
  }
}

.program-example__image {
  object-fit: cover;
  object-position: left;
  width: 100%;

  @media (min-width: $tablet-width) {
    height: 517px;
  }

  &--after {
    object-position: right;
  }
}

.program-example__image-container {
  display: flex;
  margin-top: 35px;
  margin-bottom: 28px;
  margin-left: -21px;

  @media (min-width: $tablet-width) {
    margin-top: 80px;
    margin-bottom: 0;
    margin-left: 16px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 3px;
    margin-bottom: 20px;
    margin-left: 0;
  }
}

.program-example__image-wrapper {
  width: calc(var(--slide-width) - var(--slide-shift));
  overflow: hidden;
  transition: width 1s ease-in-out;

  &--after {
    width: var(--slide-shift);
  }
}

.program-example__button-wrapper {
  margin-bottom: 37px;
}

.program-example__button {
  border: none;
  background-color: $light-grey;
  text-transform: uppercase;
}

.program-example__filter {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $desktop-width) {
    margin: 0;
    padding-bottom: 67px;
  }
}
